/*---------------------------------------------------------------
    Core 
--------------------------------------------------------------- */
* {
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  background: #fff;
  color: #333;
  font: 16px/1.5 "sans-serif", sans-serif;
  min-height: 100vh;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased; }
  body.fixed {
    overflow: hidden; }

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px; }

.wrapper {
  width: 100%; }

.title-full {
  width: 100%;
  position: relative;
  display: flex;
  text-align: center;
  align-items: center;
  padding: 20px 20px; }
  .title-full p {
    font-family: anton, sans-serif;
    font-weight: 400;
    font-style: normal;
    color: #4977BC;
    font-size: 36px;
    text-align: center;
    flex: 1;
    margin: 0 auto; }
  .title-full .line {
    height: 1px;
    background: #97B5D1;
    flex: 1; }

.title-full-description {
  display: block;
  margin: 0 auto;
  text-align: center;
  padding: 0px;
  font-family: din-2014, sans-serif;
  font-weight: 400;
  font-style: normal; }
  @media (min-width: 800px) {
    .title-full-description {
      padding: 0 50px; } }

/* For lazy loaded elements */
.lazyload,
.lazyloading {
  opacity: 0; }

.lazyloaded {
  opacity: 1;
  transition: opacity 300ms; }

.pagination-links {
  margin: 90px 0px 0px 0px; }

/*---------------------------------------------------------------
    Grid
--------------------------------------------------------------- */
.grid {
  width: 100%; }

@media (min-width: 800px) {
  .grid {
    float: left; }
  .grid1_12 {
