/*---------------------------------------------------------------
    Reset
--------------------------------------------------------------- */
* {
  margin: 0;
  padding: 0; }

*, *:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body, html {
  min-height: 101%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background: 0 0; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: .67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

hr {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: none;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  height: auto; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: 700; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

/* General Components */
.title {
  font-family: anton, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 24px; }

/*---------------------------------------------------------------
    Header
--------------------------------------------------------------- */
header .header--container {
  height: 250px;
  max-height: 240px;
  margin-bottom: -110px;
  width: 100%; }
  @media (min-width: 800px) {
    header .header--container {
      display: flex;
      align-content: space-between;
      justify-content: space-between;
      padding: 30px 40px 0px 40px;
      margin: 0;
      height: auto;
      position: absolute;
      top: 0;
      z-index: 10; } }
  header .header--container a {
    display: flex;
    justify-content: center; }

header .search {
  z-index: 3;
  display: none; }
  @media (min-width: 800px) {
    header .search {
      display: block; } }

header .search-form {
  display: flex;
  justify-content: center;
  z-index: 2; }
  @media (min-width: 800px) {
    header .search-form {
      justify-content: initial; } }
  header .search-form input {
    padding: 1.25rem 1.25rem 1.25rem 4.5rem;
    margin-bottom: 1rem;
    border: 1px solid #062A60;
    border-radius: 15px;
    background-color: #E0E4EB;
    color: #062A60;
    -webkit-appearance: none;
    font-family: din-2014, sans-serif;
    font-weight: 700;
    font-style: normal;
    text-transform: uppercase;
    position: relative; }
  header .search-form input[type="submit"] {
    color: #062A60;
    font-family: din-2014, sans-serif;
    font-weight: 700;
    font-style: normal;
    text-transform: uppercase;
    padding: 1.25rem; }
  header .search-form .screen-reader-text {
    display: none; }

.logo {
  display: block;
  width: 100%;
  max-width: 200px;
  transition: .3s;
  margin: 0 auto;
  padding: 10px 5px;
  position: absolute;
  z-index: 200; }
  .logo:hover {
    transform: rotate(5deg); }
  @media (min-width: 800px) {
    .logo {
      float: left;
      position: relative;
      height: auto;
      z-index: initial; } }

.phone-top-right {
  display: none; }
  @media (min-width: 800px) {
    .phone-top-right {
      float: right;
      display: block;
      padding: 20px; } }

/*---------------------------------------------------------------
    Hero
--------------------------------------------------------------- */
.hero {
  text-align: center;
  background-size: cover;
  background-position: center; }
  @media (min-width: 800px) {
    .hero {
      margin: 200px 0px 0px 0px; } }
  .hero__container {
    position: relative; }
    @media (min-width: 800px) {
      .hero__container {
        display: flex;
        min-height: 350px; } }
  .hero__content {
    color: #062A60; }
    @media (min-width: 800px) {
      .hero__content {
        font-family: anton, sans-serif;
        font-weight: 400;
        font-style: normal;
        z-index: 2;
        width: 45%;
        max-width: 690px;
        text-align: left;
        padding: 50px 0px 0px 40px; } }
    @media (min-width: 1000px) {
      .hero__content {
        width: 50%; } }
  .hero__primary {
    font-weight: 400;
    font-style: normal;
    font-size: 36px;
    font-family: anton, sans-serif;
    font-weight: 400;
    font-style: normal;
    padding: 10px 0px 50px 0px; }
    @media (min-width: 800px) {
      .hero__primary {
        font-size: 45px;
        line-height: 65px; } }
    @media (min-width: 1000px) {
      .hero__primary {
        font-size: 64px;
        line-height: 85px; } }
  .hero__image {
    z-index: 1;
    background-size: 100%;
    width: 100%;
    max-width: 600px;
    margin: 0 auto; }
    @media (min-width: 800px) {
      .hero__image {
        position: absolute;
        top: 0px;
        right: 0px;
        width: 470px;
        min-height: 700px;
        margin: 0; } }
    @media (min-width: 1000px) {
      .hero__image {
        width: 500px; } }
    @media (min-width: 1200px) {
      .hero__image {
        width: 550px; } }
    .hero__image img {
      width: 100%; }
      @media (min-width: 600px) {
        .hero__image img {
          width: 100%;
          height: 610px; } }
      @media (min-width: 800px) {
        .hero__image img {
          height: 600px;
          width: initial;
          margin-left: -45px; } }

/*---------------------------------------------------------------
    Mobile Top Bar
--------------------------------------------------------------- */
.mobile-top-btn {
  display: inline-block;
  float: left;
  padding: 8px;
  text-align: center;
  text-decoration: none;
  font-family: anton, sans-serif;
  font-weight: 400;
  font-style: normal;
  position: fixed;
  top: 0;
  z-index: 200; }
  @media (min-width: 1000px) {
    .mobile-top-btn {
      display: none; } }
  .mobile-top-btn--menu {
    color: #fff;
    background: #F88D9C;
    border-radius: 0px 30px 30px 0px;
    padding: 15px 30px 13px 30px; }
    .mobile-top-btn--menu i {
      font-size: 25px; }
  .mobile-top-btn--single-number {
    width: 65%; }
    .mobile-top-btn--single-number:before {
      display: inline-block;
      margin-right: 10px;
      content: '\f095';
      font-family: "FontAwesome"; }
    .mobile-top-btn--single-number a {
      color: #333; }

/*---------------------------------------------------------------
    Navigation (Desktop)
--------------------------------------------------------------- */
.desktop-nav {
  display: none; }
  @media (min-width: 1000px) {
    .desktop-nav {
      clear: both;
      display: flex;
      flex-grow: 2;
      text-align: center; }
      .desktop-nav li {
        display: inline-block;
        position: relative; }
        .desktop-nav li.menu-item-has-children > a:after {
          content: "\f107";
          position: absolute;
          bottom: -15px;
          left: 50%;
          margin-left: -8px;
          font: 1em/1 FontAwesome; }
      .desktop-nav .current-menu-item a,
      .desktop-nav .current-menu-parent a {
        color: #4977BC;
        background: #E0E4EB; }
      .desktop-nav a {
        display: inline-block;
        text-decoration: none;
        color: #4977BC;
        padding: 15px 14px;
        font-size: 1.5rem;
        font-family: din-2014, sans-serif;
        font-weight: 700;
        font-style: normal;
        text-transform: uppercase;
        border-radius: 12px 12px 12px 0px; }
        .desktop-nav a:hover {
          color: #81a1d1; } }
  @media (min-width: 1200px) {
    .desktop-nav li {
      margin-right: -4px; }
    .desktop-nav a {
      font-size: 1.6rem;
      padding: 20px 25px; } }
